import React from 'react';
import { graphql, Link } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';
import Img from 'gatsby-image';

import Layout from '../../components/Layout';
import Container from '../../components/Container';
import Footer from '../../components/Footer';
import SpaceDivider from '../../components/SpaceDivider';

import BlockCaseStudyCards from '../../components/BlockCaseStudyCards';
import BlockHeaderNoImage from '../../components/BlockHeaderNoImage';
import BlockQuote from '../../components/BlockQuote';
import BlockSteps from '../../components/BlockSteps';

import { colors } from '../../constants';

import { edgesByName } from '../../utils/gql';

export const pageQuery = graphql`
  {
    biggerBuyersImage: file(name: { eq: "bigger-buyers" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 120) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
    hyperikonLedTubes: contentfulAsset(title: { eq: "hyperikon-led-tubes" }) {
      title
      fluid {
        ...GatsbyContentfulFluid
      }
    }
    hyperikonCommercialSales: contentfulAsset(
      title: { eq: "hyperikon-commercial-sales" }
    ) {
      title
      fluid {
        ...GatsbyContentfulFluid
      }
    }
    viceGolfBrandImage: contentfulAsset(
      title: { eq: "vice-golf-brand-image" }
    ) {
      title
      fluid {
        ...GatsbyContentfulFluid
      }
    }
  }
`;

export default function CaseStudyHyperikon({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);

  return (
    <Layout>
      <GatsbySeo
        title="Hyperikon case study"
        description="How Hyperikon multiplied their b2b sales and reduced their deal closing time with Net Terms automation"
      />
      <Container className="case-study-page">
        <BlockHeaderNoImage
          pretitle={
            <>
              <Link className="case-study-page-link" to="/case-studies">
                CASE STUDIES
              </Link>
              <span> / HYPERIKON</span>
            </>
          }
          pretitleImage={companies['Hyperikon blue']}
          title="How Hyperikon multiplied their b2b sales and reduced their deal closing time with Net Terms automation"
          desc={
            <span>
              Started in 2010, Hyperikon has grown from humble beginnings to a
              provider of 250+ LED products and e-commerce brand with almost 1
              million customers on Amazon and their own website.
            </span>
          }
        />

        <Img
          fluid={data.hyperikonLedTubes.fluid}
          imgStyle={{ objectFit: 'cover' }}
          alt="wide"
          className="app__wide-image"
        />

        <BlockSteps
          wide={true}
          title="The brief"
          desc="When the leadership team at Hyperikon set out to grow their wholesale LED lighting business, they envisioned a truly seamless B2B buying experience for their business customers with Net Payment Terms."
          data={[
            {
              text:
                "Hyperikon's business customers were being forced through a difficult and lengthy credit approval process: long multi-page applications, paper and fax based processes, and multi-week decision timelines. Hyperikon wanted to change that and deliver a frictionless, customer-first approach.",
            },
            {
              text:
                'Previously, Hyperikon tried offering a cumbersome deposit payment program with their customers, rather than the ease of buying now and paying later. But they wanted to give credit terms to buy wholesale seamlessly.',
            },
          ]}
        />

        <Img
          fluid={data.hyperikonCommercialSales.fluid}
          imgStyle={{ objectFit: 'cover' }}
          alt="wide"
          className="app__wide-image"
        />

        <BlockSteps
          wide={true}
          title="The solution"
          desc="Hyperikon embedded Resolve's online application into their website and worked with Resolve team to launch marketing campaigns to drive new and repeat sales."
          data={[
            {
              title: '90% faster sales cycle',
              text: `Through Resolve's online credit application, customers were being approved the same business day. For buyers, this meant no more paper or fax applications, waiting for weeks to hear back about a credit decisions, and no more up-front deposits.`,
            },
            {
              title: '3x average order size (AOV)',
              text:
                "With higher credit lines offered through Resolve, Hyperikon wholesale customers also increased their orders to 3x their previous size to reach the bulk discount rates, a huge win for Hyperikon's growth goals.",
            },
            {
              title: '1.5 more closed b2b sales',
              text:
                'In just about a few quarters, the company’s wholesale business had rocketed from a small base of buyers to countless business customers and glowing wholesale customer feedback.',
            },
          ]}
        />

        <BlockQuote title="Account Executive at Hyperikon" author="Ray Gil">
          "Resolve has been like a spring... allowing us to grow and scale your
          B2B business more quickly than any other company. It's been a catalyst
          to faster and better growth for us."
        </BlockQuote>

        <SpaceDivider height={100} />

        <BlockCaseStudyCards
          title="More case studies"
          items={[
            {
              title: 'Vice Golf',
              description:
                'Great Heights reinvented the way conscious couples commit to forever',
              link: '/case-studies/vice-golf',
              image: data.viceGolfBrandImage.fluid,
            },
            {
              title: 'DTC ETHICAL DIAMONDS',
              description:
                'Great Heights reinvented the way conscious couples commit to forever',
              link: '/case-studies/nowvac',
              image: data.biggerBuyersImage.childImageSharp.fluid,
            },
            {
              title: 'DTC ETHICAL DIAMONDS',
              description:
                'Great Heights reinvented the way conscious couples commit to forever',
              link: '/case-studies/nowvac',
              image: data.biggerBuyersImage.childImageSharp.fluid,
            },
          ]}
        />
      </Container>
      <Footer />
      <style jsx global>{`
        .container.case-study-page {
          padding: 0;
          max-width: 100%;
        }

        .case-study-page-link {
          color: ${colors.darkSlateBlue};
        }

        .app__wide-image {
          max-width: 1000px;
          max-height: 400px;
          background: black;
          margin: 0 auto;
        }

        .block-cta .app__title {
          font-size: 42px;
          line-height: 3.5rem;
        }

        .block-cta .app__actions {
          margin-top: 20px;
        }
      `}</style>
    </Layout>
  );
}
